<template>
    <div id="app">
        <div class="window_con">
            <!-- <div class="tit">
                <div>{{title}}</div>
            </div>
            <div class="btn_close" @click="closeWindow">
                <img src="@/assets/icon_close@2x.png" />
            </div> -->
            <div id="plyrWrap">
                <vue-plyr :options="ops" ref="plyr">
                    <video :src="videoUrl">
                    </video>
                </vue-plyr>
            </div>

        </div>
    </div>
</template>
<script>

    import utils from '@/assets/utils.js';
    import API from '@/assets/api.js'
    import axios from 'axios'
    //支持Object.entries
    Object.entries = function (obj) {
        var ownProps = Object.keys(obj),
            i = ownProps.length,
            resArray = new Array(i); // preallocate the Array
        while (i--)
            resArray[i] = [ownProps[i], obj[ownProps[i]]];

        return resArray;
    };
    Object.values = function (obj) {
        let vals = [];
        for (const prop in obj) {
            vals.push(obj[prop]);
        }
        return vals;
    };


    export default {
        name: 'App',
        data() {
            return {
                dpr: 1,
                title: "",
                videoUrl: "",
                ops: {
                    controls: ['play-large', 'play', 'progress', 'current-time', 'mute', 'volume', 'settings'],
                    settings: ['speed'],
                    clickToPlay: true,
                    i18n: {
                        restart: '重新播放',
                        rewind: 'Rewind {seektime}s',
                        play: '播放',
                        pause: '暂停',
                        fastForward: 'Forward {seektime}s',
                        seek: 'Seek',
                        seekLabel: '{currentTime} of {duration}',
                        played: 'Played',
                        buffered: 'Buffered',
                        currentTime: 'Current time',
                        duration: 'Duration',
                        volume: '音量',
                        mute: 'Mute',
                        unmute: 'Unmute',
                        enableCaptions: 'Enable captions',
                        disableCaptions: 'Disable captions',
                        download: 'Download',
                        enterFullscreen: 'Enter fullscreen',
                        exitFullscreen: 'Exit fullscreen',
                        frameTitle: 'Player for {title}',
                        captions: 'Captions',
                        settings: '设置',
                        menuBack: 'Go back to previous menu',
                        speed: '播放速度',
                        normal: '正常',
                        quality: 'Quality',
                        loop: '循环播放',
                        start: '开始',
                        end: '结束',
                        all: 'All',
                        reset: '重置',
                        disabled: 'Disabled',
                        enabled: 'Enabled',
                        advertisement: 'Ad',
                        qualityBadge: {
                            2160: '4K',
                            1440: 'HD',
                            1080: 'HD',
                            720: 'HD',
                            576: 'SD',
                            480: 'SD',
                        },
                    },
                },
            }
        },
        computed: {
            player() {
                return this.$refs.plyr.player
            }
        },
        mounted() {
            //用于当视频窗口隐藏时暂停播放的方法
            window.videoPause = this.videoPause
            //获取url参数
            this.videoUrl = decodeURIComponent(utils.getUrlPrams("url"))
            this.title = utils.getUrlPrams("title")
            this.dpr = localStorage.getItem("width") / 1920
            // console.log(this.player)
            this.initPlyrIconSize()
            this.player.on('loadeddata', event => { this.player.currentTime = 0; });

        },
        beforeMount() {
            Object.entries = function (obj) {
                var ownProps = Object.keys(obj),
                    i = ownProps.length,
                    resArray = new Array(i); // preallocate the Array
                while (i--)
                    resArray[i] = [ownProps[i], obj[ownProps[i]]];

                return resArray;
            };
            Object.values = function (obj) {
                let vals = [];
                for (const prop in obj) {
                    vals.push(obj[prop]);
                }
                return vals;
            };
        },
        methods: {
            videoPause() {
                this.player.pause()
            },
            initPlyrIconSize() {
                let iconGroup = document.getElementById("plyrWrap").getElementsByTagName("svg")
                console.log(iconGroup)
                for (let i = 0; i < iconGroup.length; i++) {
                    const svg = iconGroup[i];
                    svg.setAttribute("width", 18 * this.dpr + "px")
                    svg.setAttribute("height", 18 * this.dpr + "px")
                    svg.setAttribute("viewBox", "0,0,18,18")
                }
            },
            closeWindow() {
                window.cefQuery({
                    request: "closeWindowVideo",   // 代表请求方法 example事件名称 data 为传入参数
                    onsuccess: function (response) {
                        console.log("invoke success response = " + response);
                    },
                    onFailure: function (ecode, emsg) {
                        console.log("invoke fail code = " + ecode + " msg:" + emsg);
                    }
                });
            }
        }
    }
</script>
<style>
    * {
        margin: 0;
        padding: 0;
    }

    .tit {
        height: 0.5rem;
        line-height: 0.5rem;
        font-size: 0.16rem;
        color: #2E4A66;
        font-weight: bold;
        text-align: center;
        background: #fff;
    }

    .btn_close {
        top: 0.06rem;
        right: 0.1rem;
    }

    #plyrWrap,
    .plyr,
    #plyrWrap>div {
        /* height: calc(100vh - 0.5rem); */
        height:100vh;
        display: flex;
        align-items: center;
        justify-content: space-around;
        width: 100%;
    }

    .plyr__controls .plyr__progress__container {
        flex: 1;
        min-width: 0;
    }
</style>